import React from 'react'
import { useTheme, createUseStyles } from 'react-jss'
import { Link, useStaticQuery, graphql } from 'gatsby'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => ({
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: [0, 24],
    borderBottom: [1, 'solid', theme.colors.grayscale[200]],
    boxShadow: { y: 1, blur: 4, color: 'rgba(0, 0, 0, 0.06)' },
  },
  logo: {
    width: 48,
    height: 48,
    margin: [12, 0],
    borderRadius: '50%',
    '&.frames': {
      marginRight: 16,
      backgroundColor: theme.colors.brand.primary,
    },
    '&.robowolf': {
      backgroundColor: theme.colors.brand.foreground,
    },
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 18,
  },
  nav: {
    display: 'flex',
    flex: 1,
    marginLeft: 20,
    '& ul': {
      listStyle: 'none',
      display: 'flex',
      margin: 0,
      padding: 0,
    },
  },
  link: {
    display: 'block',
    margin: [0, 20],
    padding: [22, 0],
    color: theme.colors.grayscale[300],
    fontSize: 20,
    textDecoration: 'none',
    transition: 'color .15s ease-in-out',
    '&.active': {
      color: theme.colors.brand.primary,
      borderBottom: [2, 'solid', theme.colors.brand.primary],
    },
    '&:hover': {
      color: theme.colors.brand.primary,
    },
  },
}))

const Header = () => {
  const theme = useTheme()
  const styles = useStyles(theme)
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          pages {
            path
            title
          }
        }
      }
    }
  `)

  return (
    <header className={styles.header}>
      <span className={styles.title}>
        <span className={cx(styles.logo, 'frames')}></span>
        <span>FRAMES X</span>
      </span>
      <nav className={styles.nav}>
        <ul>
          {data.site.siteMetadata.pages.map((page) => (
            <li key={page.path}>
              <Link to={page.path} className={styles.link} activeClassName="active">
                {page.title}
              </Link>
            </li>
          ))}
        </ul>
      </nav>
      <span className={cx(styles.logo, 'robowolf')}></span>
    </header>
  )
}

export default Header
